.main-container {
  max-width: 1150px;
}

.main-container__wrapper {
  margin-bottom: 30px;
  padding: 0 30px;
}

.main-container__intro {
  margin-bottom: 16px;
}

.main-container__intro--catalog {
  margin-bottom: 28px;
}

.main-container__featured {
  padding-bottom: 42px;
  margin-bottom: 31px;
}

.main-container__about {
  margin-bottom: 36px;
}

.main-container__reviews {
  padding-bottom: 52px;
  margin-bottom: 38px;
}

.main-container__making {
  margin-bottom: 58px;
}

.main-container__contacts {
  padding: 0;
  margin-bottom: 38px;
}

@media (min-width: $tablet-width) {
  .main-container__wrapper {
    padding: 0 50px;
    margin-bottom: 40px;
  }

  .main-container__intro {
    margin-top: -64px;
    margin-bottom: 66px;
  }

  .main-container__intro--form {
    margin-bottom: 54px;
  }

  .main-container__featured {
    padding-bottom: 70px;
    margin-bottom: 54px;
  }

  .main-container__about {
    padding-right: 58px;
    margin-bottom: 82px;
  }

  .main-container__reviews {
    padding-left: 0;
    padding-bottom: 86px;
    margin-bottom: 46px;
  }

  .main-container__contacts {
    padding: 0;
    margin-bottom: 58px;
  }

  .main-container__catalog {
    margin-bottom: 54px;
  }

  .main-container__making {
    margin-bottom: 64px;
  }
}

@media (min-width: $desktop-width) {
  .main-container {
    margin: 0 auto;
  }

  .main-container__intro {
    margin-bottom: 110px;
  }

  .main-container__intro--form {
    margin-bottom: 98px;
  }


  .main-container__featured {
    padding-left: 225px;
    padding-bottom: 96px;
    margin-bottom: 68px;
  }

  .main-container__about {
    padding: 0 250px 0 225px;
    margin-bottom: 88px;
  }

  .main-container__reviews {
    padding-left: 50px;
    padding-bottom: 90px;
    margin-bottom: 70px;
  }

  .main-container__contacts {
    padding: 0 50px;
    margin-bottom: 104px;
  }

  .main-container__catalog {
    margin-bottom: 56px;
  }

  .main-container__making {
    padding: 0 50px;
    margin: 0 auto 104px;
  }
}
